.error-boundary {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #ff6b6b, #ee5a24);
  color: white;
  font-family: 'Arial', sans-serif;
}

.error-content {
  text-align: center;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 40px;
  max-width: 500px;
  margin: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.luna-error {
  font-size: 4rem;
  margin-bottom: 20px;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

.error-content h2 {
  margin: 20px 0;
  font-size: 2rem;
  color: white;
}

.error-content p {
  margin: 20px 0;
  font-size: 1.1rem;
  opacity: 0.9;
  line-height: 1.5;
}

.error-actions {
  display: flex;
  gap: 15px;
  justify-content: center;
  margin: 30px 0 20px 0;
}

.retry-button,
.home-button {
  padding: 12px 24px;
  border: none;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 120px;
}

.retry-button {
  background: #4CAF50;
  color: white;
}

.retry-button:hover {
  background: #45a049;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.home-button {
  background: white;
  color: #333;
}

.home-button:hover {
  background: #f0f0f0;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.error-details {
  margin-top: 20px;
  text-align: left;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 15px;
}

.error-details summary {
  cursor: pointer;
  font-weight: bold;
  margin-bottom: 10px;
  color: #ffeb3b;
}

.error-stack {
  background: rgba(0, 0, 0, 0.5);
  color: #ff4444;
  padding: 10px;
  border-radius: 5px;
  font-size: 0.8rem;
  overflow: auto;
  max-height: 200px;
  white-space: pre-wrap;
  font-family: 'Courier New', monospace;
} 