.heart-system-test {
  padding: 20px;
  font-family: monospace;
  max-width: 1200px;
  margin: 0 auto;
}

.test-controls {
  margin-bottom: 20px;
}

.test-controls button {
  margin-right: 10px;
  padding: 10px 15px;
  border: 1px solid #ccc;
  background: #f5f5f5;
  cursor: pointer;
}

.test-controls button:hover {
  background: #e5e5e5;
}

.test-controls button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.test-layout {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.debug-panel,
.results-panel {
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 5px;
}

.debug-info {
  background-color: #f5f5f5;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 15px;
}

.debug-info div {
  margin-bottom: 5px;
}

.wrong-answers-list {
  max-height: 300px;
  overflow-y: auto;
}

.wrong-answer-item {
  border: 1px solid #ccc;
  padding: 8px;
  margin: 5px 0;
  border-radius: 3px;
  background: #fafafa;
}

.wrong-answer-item div {
  margin-bottom: 3px;
}

.test-results {
  max-height: 400px;
  overflow-y: auto;
}

.test-result {
  padding: 10px;
  margin: 5px 0;
  border-radius: 3px;
}

.test-result.pass {
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
}

.test-result.fail {
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
}

.test-result.info {
  background-color: #e2e3e5;
  border: 1px solid #d6d8db;
}

.test-result.start {
  background-color: #fff3cd;
  border: 1px solid #ffeaa7;
}

.test-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.test-name {
  font-weight: bold;
}

.test-status {
  font-size: 12px;
  padding: 2px 6px;
  border-radius: 3px;
  color: white;
}

.status-pass {
  background-color: #28a745;
}

.status-fail {
  background-color: #dc3545;
}

.status-info {
  background-color: #6c757d;
}

.status-start {
  background-color: #ffc107;
  color: black;
}

.test-details {
  font-size: 14px;
  margin-bottom: 5px;
}

.test-time {
  font-size: 12px;
  color: #666;
} 